import React, {Component, Fragment} from 'react';
import {Button, Col, Container, Form, Row} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEnvelope, faPhone} from "@fortawesome/free-solid-svg-icons";

class ContactSection extends Component {
    render() {
        return (
            <Fragment>
                <Container className="mt-5">
                    <Row>
                        <Col lg={6} md={6} sm={12}>
                            <h1 className="ContactTitle">Quick Connect</h1>

                            <Form>
                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label className="contentDesc">Name</Form.Label>
                                    <Form.Control type="text" />
                                </Form.Group>
                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label className="contentDesc">Email</Form.Label>
                                    <Form.Control type="email" />
                                </Form.Group>
                                <Form.Group controlId="formBasicEmail">
                                    <Form.Label className="contentDesc">Message</Form.Label>
                                    <Form.Control as="textarea" rows={3}/>
                                </Form.Group>
                                <Button variant="primary" type="submit">
                                    Submit
                                </Button>
                            </Form>

                        </Col>
                        <Col lg={6} md={6} sm={12}>
                            <h3 className="footerTitle">Discuss Now</h3>
                            <p className="text-muted">#79/6 Padma Residential Aria, 3rd Floor Front Side, Rajshahi</p>
                            <a className="footerDesc text-muted" href="#"><FontAwesomeIcon icon={faEnvelope} /> g.shanto18@gmail.com</a><br/>
                            <a className="footerDesc text-muted" href="#"><FontAwesomeIcon icon={faPhone} /> +8801789096018</a>

                        </Col>
                    </Row>
                </Container>
            </Fragment>
        );
    }
}

export default ContactSection;